@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Bebas Neue', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  font-size: 62.5%;
  line-height: 1.7;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*Colour scheme:
#c7493a-light orange
#a33327-dark orange
#689775-pastel green
#917164-dusty
#ad8174-light brown
deep blue: #292942
light blue: #1756ca
*/

.content{
    position: relative;
    padding-top: 3.5rem;    /*space occupied by navbar*/
}
.Navbar{
    width: 100%;
    height: 3.5rem;
    position: fixed;
    background-color: #ad8174;
    z-index: 1;
}

.logo-img{
    height: 80%;
    margin-top: 0.3rem;
    float: left;
    margin-left: 1rem;
}

.cart-img{
    height: 2rem;
    margin-top: 0.6rem;
    float:right;
    margin-right: 1rem;
}


.cover-img{
    width: 100%;
    height: 100vh;
}

.cover{
    position: relative;
}

.subtitle{
    font-size: 3rem;
    text-align: center;
    color: #689775;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    text-shadow: 0.3rem 0.3rem 0.5rem #292942;
}

.category{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.cat-intro{
    font-size: 2rem;
    margin: 2rem;
    color: #ad8174;
}
.category a{
    text-decoration: none;
    font-size: 3rem;
    padding: 1rem;
    margin: 1rem;
    background-color: #a33327;
    color: #689775;
    border-radius: 1rem;
}
.prodCard{
    margin: 1rem;
    padding: 1rem;
    background-color: #689775;
    border-radius: 0.5rem;
    text-align: center;
    color: #292942;
    width: 45%;
}

.title{
    font-size: 1.5rem;
    color: #a33327;
}

.image img{
    height: 15rem;
    width: 80%;
    display: inline-block;
    margin: 1rem;;
}

.desc{
    font-size: 1rem;
}

.price{
    font-size: 1.2rem;
    color: #1756ca;
    margin-top: 1rem;
}
.add-btn{
    border: none;
    padding: 1rem;
    margin: 1rem;
    background-color: #a33327;
    color: #ad8174;
    font-weight: 900;
}

.add-btn:hover{
    background-color: #ad8174;
    color: #a33327;
    border: 0.2rem solid #a33327;
    padding: 0.8rem;
}

.counter{
    display: flex;
    justify-content: center;
    margin: 1.5rem;
}

.down-btn, .up-btn{
    height: 1rem;
    margin: 0.5rem;
}

.count{
    font-size: 1.2rem;
    margin: 0.5rem;
}
.cart-list{
    background-color: #689775;
    width: 70%;
    margin: auto;
    border-radius: 2rem;
    padding: 1rem 3rem;
}

.cartCard{
    display: flex;
    padding: 1rem;
    margin: 1rem;
    border-bottom: #292942 solid ;
}

.item-img img{
    height: 12rem;
}

.item-img{
    margin: 1rem;
}

.item-details{
    margin: 1rem;
    width: 100%;
}

.qty{
    font-size: 1.2rem;
    text-align: right;
}

.total-price{
    font-size: 2rem;
    margin: 1rem;
    text-align: right;
    color: #1d6cff;
}
